export const Container = () => {
    var con = document.getElementById("container");
    var s_con = document.querySelectorAll(".s_content");
    var singlecom=document.querySelectorAll(".singleComponet");
    var ch = s_con.length;
    var h = ch * 32;
    var end_sc=220+h*10;
    con.style.height = h + 10 + "vh";
    con.style.opacity = "0";
    var body = document.getElementsByTagName("body")[0];
    window.addEventListener("scroll", function () {
        var scrollpos = window.scrollY;
        if (scrollpos >= 220 && scrollpos <= end_sc) {
            con.style.opacity = "1";
            var t = 1;
            for (var i = 0; i < s_con.length; i++) {
                s_con[i].style.animation = "n 1s linear " + t + "s forwards";
                s_con[i].style.marginTop = "10px";
                t = t + 0.5;
            }
        } else {
            con.style.opacity = "0";
            var s=1;
            for (var j = 0; j < s_con.length; j++) {
                s_con[j].style.animation = "alt 1s linear " + s + "s forwards";
                s = s + 0.5;
            }
        }
    });
    
}