import React from 'react';
import {ImageArray} from './imageArray';
import {MarRun} from './MarStyle';
const Slider=()=>{
    const SliderComp=ImageArray.map((image,i)=>{
       return (
            <li style={{height:'50vh', width:'20vw',flexShrink:'0'}} key={i}><img style={{height:'100%',width:'100%'}} src={require(`./image/${image}`)} alt="pictuers"/></li>
       ); 
    });
    return <ul id="mar" onLoad={MarRun}>{SliderComp}</ul>;
}
export default Slider;