import React from 'react';
import {Container} from './container';

const Content=(props)=>{
    const SingleContent=()=>{
        return(
            <div className="s_content" style={{display:'flex',border:'2px solid black',transform:'translateX(-100px)',opacity:'0',alignItems:'center',height:'30vh',width:'90vw',overflow:'hidden',justifyContent:'center',margin:'0 auto'}}>
                <div style={{height:'30vh',width:'20vw'}}>
                    <img style={{height:'100%',width:'100%'}} src={props.src}/>
                </div>
                <div style={{height:'30vh',width:'50vw',marginLeft:'auto',alignItems:'center'}}>
                    <p style={{marginTop:'50px'}}>{props.text}</p>
                </div>
            </div>
        );
    }
    return <div className="singleComponet" onLoad={Container}>{SingleContent()}</div>;
}
export default Content;