export const MarRun=()=> {
    var mar_container = document.getElementById("mar");
    mar_container.style.display = "flex"; 
    mar_container.style.listStyle="none";
    mar_container.style.width="80vw";
    mar_container.height="50vh";
    mar_container.style.animation="sc 18s linear infinite";
    mar_container.addEventListener("mouseenter",function(){
       mar_container.style.animationPlayState="paused"; 
    });
    mar_container.addEventListener("mouseleave",function(){
        mar_container.style.animationPlayState="running";
    });
}

